<template>
  <div>
    <div v-show="!horizontal" class="flowchart-node" :style="nodeStyle"
         @mousedown="handleMousedown"
         @mouseover="handleMouseOver"
         @mouseleave="handleMouseLeave"
         v-bind:class="{selected: options.selected === id}">
      <div class="node-port node-input"
           @mousedown="inputMouseDown"
           @mouseup="inputMouseUp">
      </div>
      <div class="node-main">
        <div v-text="label" class="node-label"></div>
        <div v-text="content" class="node-content"></div>
      </div>
      <div class="node-port node-output"
           @mousedown="outputMouseDown">
      </div>
      <div v-show="show.delete" class="node-delete">&times;</div>
    </div>

    <div v-show="horizontal" class="flowchart-node" :style="nodeStyle"
         @mousedown="handleMousedown"
         @mouseover="handleMouseOver"
         @mouseleave="handleMouseLeave"
         v-bind:class="{selected: options.selected === id}">
      <div class="node-port node-input"
           v-show="nodesWithoutInput(type)"
           @mousedown="inputMouseDown"
           @mouseup="inputMouseUp"
           :style="'width: '+portSize+'; height: '+portSize+';'"
      >
      </div>

      <slot name="content">
        <div class="node-main">
          <div class="node-label" :style="'background: '+colorMap[type]">
              {{label}}
          </div>
          <div v-text="content" class="node-content"></div>
        </div>
        <div class="node-port node-output"
             v-show="nodesWithoutOutput(type)"
             @mousedown="outputMouseDown"
             :style="'width: '+portSize+'; height: '+portSize+';'"
        >
        </div>
      </slot>

      <div v-show="show.delete"
           class="node-delete"
           :style="'width: '+portSize+'; height: '+portSize+';'"
      >&times;</div>
    </div>


  </div>
</template>

<script>
export default {
  name: 'FlowchartNode',
  props: {
    id: {
      type: Number,
      default: 1000,
      validator(val) {
        return typeof val === 'number'
      }
    },
    x: {
      type: Number,
      default: 0,
      validator(val) {
        return typeof val === 'number'
      }
    },    
    y: {
      type: Number,
      default: 0,
      validator(val) {
        return typeof val === 'number'
      }
    },
    type: {
      type: String,
      default: 'Default'
    },
    label: {
      type: String,
      default: 'input name'
    },
    content: {
      type: String,
      default: ''
    },
    lineContent: {
      type: String,
      default: ''
    },
    options: {
      type: Object,
      default() {
        return {
          centerX: 1024,
          scale: 1,
          centerY: 140,
          content: '',
        }
      }
    },
    horizontal: {type: Boolean, default: false},
    colorMap: {
      type: Object,
      default(){}
    },
    nodeWidth: {
      type: Number,
      default: 120
    },
    nodeHeight: {
      type: Number,
      default: 80
    },
    portSize: {
      type: Number,
      default: 20
    },
  },
  data() {
    return {
      show: {
        delete: false,
      }
    }
  },
  mounted() {
  },
  computed: {
    nodeStyle() {
      return {
        top: this.options.centerY + this.y * this.options.scale + 'px', // remove: this.options.offsetTop + 
        left: this.options.centerX + this.x * this.options.scale + 'px', // remove: this.options.offsetLeft + 
        transform: `scale(${this.options.scale})`,
        width: this.nodeWidth + 'px',
        height: this.nodeHeight + 'px'
      }
    }
  },
  methods: {
    handleMousedown(e) {
      const target = e.target || e.srcElement;
      // console.log(target);
      if (target.className.indexOf('node-input') < 0 && target.className.indexOf('node-output') < 0) {
        this.$emit('nodeSelected', e);
      }
      e.preventDefault();
    },
    handleMouseOver() {
      this.show.delete = true;
    },
    handleMouseLeave() {
      this.show.delete = false;
    },
    outputMouseDown(e) {
      this.$emit('linkingStart')
      e.preventDefault();
    },
    inputMouseDown(e) {
      e.preventDefault();
    },
    inputMouseUp(e) {
      this.$emit('linkingStop')
      e.preventDefault();
    },
    nodesWithoutInput(type){
      switch(type){
        case 'source':
        case 'timer':
          return false;
      }
      return true;
    },
    nodesWithoutOutput(type){
      switch(type){
        case 'alert':
        case 'beeper':
        case 'action':
          return false;
      }
      return true;
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
$themeColor: rgb(255, 136, 85);
$portSize: 20;
$halfPortSize: 10;
$nodeHeight: 80;
$nodeWidth: 120;

.float-left {
  float: left;
}

.flowchart-node {
  margin: 0;
  width: #{$nodeWidth}px;
  height: #{$nodeHeight}px;
  position: absolute;
  box-sizing: border-box;
  border: none;
  background: white;
  z-index: 1;
  opacity: .9;
  cursor: move;
  transform-origin: top left;
  .node-main {
    text-align: center;
    .node-label {
      background: $themeColor;
      color: white;
      font-size: 1.2rem;
      padding: 6px;
    }
    .node-content {
      font-size: 1rem;
      padding-top: 6px;
    }
  }
  .node-port {
    position: absolute;
    width: #{$portSize}px;
    height: #{$portSize}px;

    border: 1px solid #ccc;
    border-radius: 100px;
    background: white;
    &:hover {
      background: $themeColor;
      border: 1px solid $themeColor;
    }
  }
  .node-input {
    right: 100%;
    top: 50%-$halfPortSize;
    background-color: deepskyblue;
  }
  .node-output {
    left: 100%;
    top: 50%-$halfPortSize;
    background-color: cadetblue;
  }

  .node-true {
    left: 100%;
    top: 50%-$halfPortSize;
    background-color: green;
  }

  .node-false {
    left: 100%;
    top: 50%-$halfPortSize;
    background-color: red;
  }

  .node-delete {
    position: absolute;
    right: -6px;
    top: -6px;
    font-size: 12px;
    width: 1rem;
    height: 1rem;
    color: $themeColor;
    cursor: pointer;
    background: white;
    border: 1px solid $themeColor;
    border-radius: 100px;
    text-align: center;
    &:hover{
      background: $themeColor;
      color: white;
    }
  }
}
.selected {
  box-shadow: 0 0 0 2px $themeColor;
}
</style>
